* {
    margin: 0;
    padding: 0;
}

body {
    position: relative;
    background: url(../img/bilde-test-1.jpg) no-repeat center;
    background-size: cover;
    height: 100vh;
    overflow: hidden;
}

.game-grid {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.main {
    display: flex;
    align-items: center;
    justify-content: center;
}

.game-details {
    display: flex;
    align-items: center;
    justify-content: center;
}

.game-grid {
    display: flex;
    align-items: center;
    justify-content: center;
}

table {
    border-collapse: collapse;
    border: 4px solid #EEC79B;
    background-color: #17651787;
}


td {
    height: 70px;
    width: 70px;
    border: 2px solid #EEC79B;
}

.obstacle {
    background: url('../img/block.png');
}

.players {
    width: 71px;
    height: 71px;
}

.player1 {
    background-size: contain!important;
    background: url('../img/link.png') no-repeat center;
}

.player2 {
    background: url('../img/Ganondorf2.png')  no-repeat center;
    background-size: contain;
}

.weapon0 {
    background: url('../img/arc-royal.png')  no-repeat center;
    background-size: contain;
}

.swal2-container {
    position:relative!important;
   }

.swal2-container.swal2-backdrop-show,
.swal2-container.swal2-noanimation {
 background:transparent !important;
}

   